'use client';

// Web Audio Utility using Server-Side Piper TTS

let currentAudio: HTMLAudioElement | null = null;

const PHONEME_MAP: Record<string, string> = {
  "a": "aaa",
  "b": "buh",
  "c": "kuh",
  "d": "duh",
  "e": "eh",
  "f": "fuh",
  "g": "guh",
  "h": "huh",
  "i": "ih",
  "j": "juh",
  "k": "kuh",
  "l": "luh",
  "m": "muh",
  "n": "nuh",
  "o": "ah",
  "p": "puh",
  "q": "kuh",
  "r": "ruh",
  "s": "suh",
  "t": "tuh",
  "u": "uh",
  "v": "vuh",
  "w": "wuh",
  "x": "ks",
  "y": "yuh",
  "z": "zuh",
  "sh": "shh",
  "ch": "chh",
  "th": "thh",
  "wh": "whh",
  "ng": "ng",
  "ck": "kuh",
  "qu": "kwuh",
  "ff": "fuh",
  "ll": "luh",
  "ss": "suh",
  "bl": "bluh",
  "cl": "kluh",
  "fl": "fluh",
  "pl": "pluh",
  "sl": "sluh",
  "gl": "gluh",
  "br": "bruh",
  "cr": "kruh",
  "dr": "druh",
  "fr": "fruh",
  "gr": "gruh",
  "pr": "pruh",
  "tr": "truh",
  "sc": "skuh",
  "sk": "skuh",
  "sm": "smuh",
  "sn": "snuh",
  "sp": "spuh",
  "st": "stuh",
  "sw": "swuh",
};

export function textToPhoneme(text: string): string {
  const lower = text.toLowerCase().trim();
  return PHONEME_MAP[lower] || text;
}

export async function speakText(text: string, options?: { rate?: number; pitch?: number; usePhoneme?: boolean; interrupt?: boolean }): Promise<void> {
  // Convert rate (speed) to Piper's length_scale (slowness)
  // rate 1.0 -> length_scale 1.0
  // rate 0.5 -> length_scale 2.0 (2x slower)
  // rate 0.1 -> length_scale 10.0 (10x slower)
  const rate = options?.rate || 1.0;
  const lengthScale = Math.max(0.1, 1 / rate).toFixed(2); // Ensure valid scale

  const shouldInterrupt = options?.interrupt ?? true;
  if (shouldInterrupt) {
    stopSpeaking();
  }

  const textToSpeak = options?.usePhoneme ? textToPhoneme(text) : text;
  
  // Call API
  const url = `/api/tts?text=${encodeURIComponent(textToSpeak)}&speed=${lengthScale}`;

  return new Promise((resolve) => {
    const audio = new Audio(url);
    
    // If interrupting, set as current
    if (shouldInterrupt) {
        currentAudio = audio;
    }

    audio.onended = () => {
      if (currentAudio === audio) currentAudio = null;
      resolve();
    };

    audio.onerror = (e) => {
      console.error("Audio playback error:", e);
      resolve();
    };

    audio.play().catch(e => {
      console.warn("Autoplay blocked or network error:", e);
      resolve();
    });
  });
}

export function stopSpeaking(): void {
  if (currentAudio) {
    currentAudio.pause();
    currentAudio = null;
  }
  // Also cancel synthesis just in case legacy code used it
  if (typeof window !== 'undefined' && window.speechSynthesis) {
    window.speechSynthesis.cancel();
  }
}

export function getStretchedWord(word: string): string {
  // Legacy helper - mostly unused with Piper's native stretching
  return word; 
}

// Helper to play raw audio files
export function playAudio(src: string): Promise<void> {
    stopSpeaking();
    return new Promise((resolve, reject) => {
        const audio = new Audio(src);
        currentAudio = audio;
        audio.onended = () => {
            currentAudio = null;
            resolve();
        };
        audio.onerror = (e) => reject(e);
        audio.play().catch(reject);
    });
}